<div class="breadcrumbs"><ul class="breadcrumb-list" v-if="breadcrumbs.length&gt;1"><li class="breadcrumb-item" v-for="(breadcrumb, index) in breadcrumbs"><router-link class="link" :to="breadcrumb.href" v-if="index!==breadcrumbs.length-1">{{ breadcrumb.text }}</router-link></li></ul><div class="current-breadcrumb">{{ breadcrumbs[breadcrumbs.length-1].text }}</div></div>