<AppLayout :breadcrumbs="breadcrumbs"><div class="loader" v-if="deliveryPoint.loading">Загрузка</div><div class="error" v-else-if="deliveryPoint.error">{{deliveryPoint.error}}</div><div class="delivery-point" v-else-if="deliveryPoint.data"><div class="left-side"><ScanProgress :cityName="deliveryPoint.data.shortName" :scannedOrdersCount="rightOrders.length" :ordersCount="orders.data.length"></ScanProgress></div><div class="right-side"><CurrentScan class="current-scan" :scannedOrdersCount="rightOrders.length" :ordersCount="orders.data.length" :scanningOrder="scanningOrder" :scanningResult="scanningResult"></CurrentScan><div class="orders"><div class="title">Неотсканировано</div><TransitionFade><div class="loader" v-if="orders.loading" key="load">Загрузка</div><div class="error" v-else-if="orders.error" key="error">{{orders.error}}</div><div class="empty-list" v-else-if="orders.loaded &amp;&amp; orderListIsEmpty" key="empty">Нет заказов</div><div class="result" v-else-if="allOrdersScanned" key="result"><div class="title">Отлично, приступайте к отгрузке! </div><div class="message">Проверенные заказы соответсвуют ПВЗ.</div></div><OrderList v-else :orders="notScannedOrders" key="orders" v-model="scanningOrder" @update:modelValue="scan"></OrderList></TransitionFade></div></div></div></AppLayout>