<div class="current-scan"> <div class="title-wrapper"><div class="title">Текущее сканирование</div><span class="status">{{scannedOrdersCount}} из {{ordersCount}}</span></div><transition name="bounce" mode="out-in"><div class="warn" v-if="scanningResult===null" key="warn"><div class="text">Начните сканировать заказы для проверки</div><div class="status-image-wrapper"><img src="@/assets/images/scan.svg"></div></div><div class="error" v-else-if="scanningResult===false" :key="scanningOrder.orderId"><div class="text"><div class="id">Номер заказа: {{scanningOrder.orderId}}</div><div class="city">Город: <span class="city-name">{{scanningOrder.deliveryInfo.deliveryPoint.shortName}}</span></div><div class="customer">Покупатель: {{fullName}}</div></div><div class="status-image-wrapper"><img src="@/assets/images/error.svg"></div></div><div class="info" v-else :key="scanningOrder.orderId"><div class="text"><div class="id">Номер заказа: {{scanningOrder.orderId}}</div><div class="city">Город: <span class="city-name">{{scanningOrder.deliveryInfo.deliveryPoint.shortName}}</span></div><div class="customer">Покупатель: {{fullName}}</div></div><div class="status-image-wrapper"><img src="@/assets/images/success.svg"></div></div></transition></div>